import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Row, Col, Breadcrumb, Menu } from 'antd';

class Index extends React.Component {
    constructor(props) {
        super(props);
        ['changeMenu'].forEach(m=>this[m] = this[m].bind(this));
    }

    changeMenu(obj) {
        hashHistory.push(obj.key);
    }

    render() {
        return (
            <div className="aboutPage">
                <Row className="contentFrame">
                    <Breadcrumb>
                        {this.props.routes.map(item =>
                            <Breadcrumb.Item key={item.breadcrumbName}>{item.breadcrumbName}</Breadcrumb.Item>
                        )}
                    </Breadcrumb>
                    <Row className="detail">
                        <Col span={4} className="menu">
                            <Menu
                                defaultSelectedKeys={[this.props.location.pathname]}
                                mode="inline"
                                theme="light"
                                onClick={this.changeMenu}
                            >
                                <Menu.Item key="/about/overview">公司概况</Menu.Item>
                                <Menu.Item key="/about/news">行业动态</Menu.Item>
                                <Menu.Item key="/about/report">媒体报道</Menu.Item>
                                <Menu.Item key="/about/contact">联系我们</Menu.Item>
                            </Menu>
                        </Col>
                        <Col span={20} className="detailFrame">
                            {this.props.children}
                        </Col>
                    </Row>
                </Row>
            </div>
        )
    }
}

module.exports = Index;


